export default{
	data(){
		return{
			avatarUrl:'../../static/icon/avatar.png',//头像
			nickName:'点击登录',
			token:'',
			loginState:'',
		}
	},
	onLoad() {
		
	},
	onShow() {
		
		this.token = uni.getStorageSync('token');
		
	},
	methods:{
		openinfo(e) {
		    console.log(e)
		    uni.navigateTo({
		        url: '../login/login',
		        success: res => {},
		        fail: () => {},
		        complete: () => {}
		    });
		},
		//点击登录
		BindGetUserInfo:function(){
		
			if ( this.loginState == 1 ) {
				
			} else{
				let that = this;
				wx.getUserInfo({
					success: function (res) {
						//获取到的微信头像 昵称 性别
						that.nickName = res.userInfo.nickName;
						that.avatarUrl = res.userInfo.avatarUrl;
						that.loginState = 1;
					}
				});
			}
		},			
	}
}
